<template>
  <div class="modal fade" id="exampleModal" tabindex="-1"
    aria-labelledby="exampleModalLabel" aria-hidden="true"
    ref="modal">
    <div class="modal-dialog modal-xl" role="document">
      <div class="modal-content border-0">
        <div class="modal-header bg-dark text-white">
          <h5 class="modal-title" id="exampleModalLabel">
            <span>檢視訂單</span>
          </h5>
          <button type="button" class="btn-close"
                  data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-sm-4">
              <table class="table">
                  <thead>
                    <tr>
                      <th colspan="4" class="fs-2 text-left">用戶資料</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <th scope="row">姓名</th>
                      <td colspan="3">{{tempOrder.user.name}}</td>
                    </tr>
                    <tr>
                      <th scope="row">Email</th>
                      <td colspan="3">{{tempOrder.user.email}}</td>
                    </tr>
                    <tr>
                      <th scope="row">電話</th>
                      <td colspan="3">{{tempOrder.user.tel}}</td>
                    </tr>
                    <tr>
                      <th scope="row">地址</th>
                      <td colspan="3">{{tempOrder.user.address}}</td>
                    </tr>
                  </tbody>
              </table>
            </div>
            <div class="col-sm-8">
              <div class="mb-6">
                <table class="table">
                  <thead>
                    <tr>
                      <th colspan="8" class="fs-2 text-left">訂單細節</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <th scope="row">訂單編號</th>
                      <td colspan="7">{{tempOrder.id}}</td>
                    </tr>
                    <tr>
                      <th scope="row">下單時間</th>
                      <td colspan="7">{{$filters.ToLocalDate(tempOrder.create_at)}}</td>
                    </tr>
                    <tr>
                      <th scope="row">付款時間</th>paid_date
                      <td colspan="3">{{tempOrder.paid_date?$filters.ToLocalDate(tempOrder.paid_date):''}}</td>
                    </tr>
                    <tr>
                      <th scope="row">付款狀態</th>
                      <td colspan="3" class="{'success':order.is_paid}">{{tempOrder.is_paid?'已付款':'未付款'}}</td>
                    </tr>
                    <tr>
                      <th scope="row">總金額</th>
                      <td colspan="3">{{tempOrder.total?$filters.currency(tempOrder.total):''}}</td>
                    </tr>
                  </tbody>
              </table>
              </div>
              <div class="mb-6">
                <table class="table">
                  <thead>
                    <tr>
                      <th colspan="8" class="fs-2 text-left">選購商品</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(product, index) in tempOrder.products" v-bind:key="'key'+index">
                      <th scope="row" colspan="4">{{product.id}}</th>
                      <td colspan="4">{{product.product_id}}</td>
                      <td colspan="4">{{product.qty}}</td>
                    </tr>
                  </tbody>
              </table>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-outline-secondary"
                  data-bs-dismiss="modal">取消
          </button>
          <button type="button" class="btn btn-primary">確認</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Modal from '../../node_modules/bootstrap/js/dist/modal'

export default {
  props: {
    order: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  data () {
    return {
      modal: {},
      tempOrder: {}
    }
  },
  mounted () {
    this.modal = new Modal(this.$refs.modal)
  },
  methods: {
    showModal () {
      this.modal.show()
    },
    hideModal () {
      this.modal.hide()
    }
  },
  watch: {
    order (n, o) {
      this.tempOrder = this.order
    }
  }
}
</script>
